@media (max-width: 1730px) {
	.header {
		&__content {
			padding-left: 10vw;
			padding-right: 10vw; } }
	.section {
		&__container {
			padding-left: 10vw;
			padding-right: 10vw; } }
	.strip {
		margin-right: 10vw; }
	.strip-nav {
		left: -5vw; }
	.nav {
			padding-left: 10vw;
			padding-right: 10vw; } }

@media (max-width: 1460px) {
	.header {
		&__content {
			height: 85vh; } }
	.choose-items__item {
		margin-right: 4.3vw;
		flex-basis: 27%; } }

@media (max-width: 1305px) {
	.header {
		&__content {
			padding-left: 5vw;
			padding-right: 5vw; } }
	.section {
		&__container {
			padding-left: 5vw;
			padding-right: 5vw; } }
	.strip {
		margin-right: 5vw; }
	.nav {
		padding-left: 5vw;
		padding-right: 5vw; }

	.strip-nav {
	    position: static;
	    transform: none;
	    width: 40px;
	    // padding-right: 10vw
	    margin-top: 20px; } }

@media (max-width: 1095px) {
	body {
		font-size: 18px; }
	.info-block {
		display: none; }
	h1 {
		font-size: 80px; }
	.header__content__description {
		font-size: 20px;
		margin-top: 30px; }
	h2 {
		font-size: 45px; }
	h3 {
		font-size: 20px; }

	.bottom-content {
		&.welcome {
			img {
				max-width: 75%;
				&:nth-child(2) {
					max-width: 50%; } } } } }

@media (max-width: 850px) {
	.choose-items__item {
		flex-basis: 100%;
		margin-right: 0px; }
	.bottom-content {
		&.welcome {
			img {
				max-width: 100%;
				&:nth-child(2) {
					display: none; } } } }
	.top-content {
		width: 100%; }

	.main-footer {
		&__content {
			flex-direction: column;
			&__left-content, &__right-content {
				width: 100%; }
			&__right-content {
				margin-top: 30px; } } }
	.section {
		&__container {
			padding-top: 60px;
			&.last {
				padding-bottom: 60px; } } }
	.bottom-content {
		margin-top: 40px; }
	.img-outer {
		height: 221px;
		width: 284px; }
	.strip-nav__line {
		width: 100%;
		height: 1px;
		background: rgba(#292B33, 0.2); }
	.input-wrapper {
		margin-top: 40px; }
	.waves-wrapper {
		bottom: -35vh; }
	.footer__container {
		padding-bottom: 60px; }
	.strip-nav {
		width: 100%;
		padding-right: calc(5vw + 20px);
		&__left {
			margin-right: 20px; }
		&__right {
			margin-left: 20px; } }
	.nav__items {
		display: none;
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 100;
		&__wrapper {
			height: 100%;
			width: 100%;
			flex-direction: column;
			background: $main-color;
			align-items: center;
			text-align: center;
			padding-top: 100px;
			padding-bottom: 100px; }
		&.show {
			display: flex; } }
	.nav__item {
		margin-left: 0px;
		a {
			color: white; } }
	.strip-link {
		font-size: 18px; } }

@media (max-width: 460px) {
	.header {
		&__content {
			height: auto;
			padding-top: 100px;
			padding-bottom: 35px; }
		&__video {
			height: 60vh;
			object-fit: cover;
			video {
				object-fit: cover;
				height: 100%; } }
		h1 {
			font-size: 60px; }
		.header__content__description {
			font-size: 18px;
			margin-top: 10px; } }
	.img-outer {
		height: 246px;
		width: 290px; }
	.waves-wrapper {
		bottom: -10vh; }
	.strip__item {
		padding-right: 20px; }
	.bottom-content {
		&.welcome {
		    margin-left: -5vw;
    		width: 100vw;
    		img {
    			height: 69vh;
    			object-fit: cover; } } }
	.choose-items__item {
    	margin-top: 30px; }
	.strip-after {
    	width: 95%; } }
