  }
  .img-outer {
    height: 221px;
    width: 284px;
  }
  .strip-nav__line {
    width: 100%;
    height: 1px;
    background: rgba(41, 43, 51, 0.2);
  }
  .input-wrapper {
    margin-top: 40px;
  }
  .waves-wrapper {
    bottom: -35vh;
  }
  .footer__container {
    padding-bottom: 60px;
  }
  .strip-nav {
    width: 100%;
    padding-right: calc(5vw + 20px);
  }
  .strip-nav__left {
    margin-right: 20px;
  }
  .strip-nav__right {
    margin-left: 20px;
  }
  .nav__items {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
